<template>
  <div class="orders mt-3">
    <table class="table table-striped">
      <thead class="thead-dark">
        <tr>
          <th scope="col">訂單編號</th>
          <th scope="col">購買項目</th>
          <th scope="col" class="text-center">付款方式</th>
          <th scope="col" class="text-right">應付金額</th>
          <th scope="col">是否付款</th>
          <th scope="col">下單時間</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in orders" :key="item.id">
        <td>{{ item.created.timestamp }}</td>
          <td>
            <ul class="list-unstyled">
              <li v-for="(product, key) in item.products" :key="key">
                {{ product.product.title }} 數量：{{ product.quantity }}
                {{ product.product.unit }}
              </li>
            </ul>
          </td>
          <td class="text-center">{{ item.payment }}</td>
          <td class="text-right">{{ Math.round(item.amount) }}</td>
          <td>
            <div class="custom-control custom-switch">
              <input
                type="checkbox"
                class="custom-control-input"
                :id="item.id"
                v-model="item.paid"
                @change="setOrderPaid(item)"
              />
              <label class="custom-control-label" :for="item.id">
                <span class="text-success font-weight-bolder" v-if="item.paid"
                  >已付款</span
                >
                <span v-else>尚未付款</span>
              </label>
            </div>
          </td>
          <td scope="row">{{ item.created.datetime }}</td>
          <td>
            <b-button v-b-modal.aaa @click="getOrder_List(item.id)"
              >查看詳細資料</b-button
            >
          </td>
        </tr>
      </tbody>
    </table>


    <b-modal id="aaa" centered title="商品內容" hide-footer>
      <div class="form-row">
        <div class="form-group col-md-6">
          <label for="name">收件人</label>
          <input
            type="text"
            id="name"
            class="form-control"
            v-model="order_user.name"
          />
        </div>
        <div class="form-group col-md-6">
          <label for="tel">收件人手機</label>
          <input
            type="text"
            id="tel"
            class="form-control"
            v-model="order_user.tel"
          />
        </div>
      </div>
      <div class="form-group">
        <label for="address">寄送地址</label>
        <input
          type="text"
          id="address"
          class="form-control"
          v-model="order_user.address"
        />
      </div>
      <div class="form-group">
        <label for="email">電子郵件</label>
        <input
          type="text"
          id="email"
          class="form-control"
          v-model="order_user.email"
        />
      </div>
      
      <hr />
      <button class="btn btn-block" type="submit">儲存變更</button>
    </b-modal>


    <pagination
      :pages="pagination"
      @update="getOeders"
      v-if="orders[0]"
    ></pagination>
  </div>
</template>
  
  <script>
import pagination from "@/components/MyPagination.vue";

export default {
  name: "MyOrders",
  data() {
    return {
      orders: [],
      pagination: {},
      isLoading: false,
      order_user: []
    };
  },
  components: {
    pagination,
  },
  created() {
    this.getOeders();
  },
  methods: {
    getOeders(page = 1) {
      this.isLoading = true;
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/orders?page=${page}`;
      this.$http.get(api).then((res) => {
        this.orders = res.data.data;
        this.pagination = res.data.meta.pagination;
      });
    },
    getOrder_List(item) {
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/orders/${item}`;
      this.$http.get(api).then((res) => {
        this.order_user = res.data.data.user;
      });
    },
  },
};
</script>
  